
<template>
  <div>
    <insert-card >
      <b-form  @reset="onReset" v-if="show">
        <b-form-group id="productName" label="Product Name:" label-for="productName">
          <b-form-input
            id="productName"
            type="text"
            v-model="form.name"
            required
            placeholder="Insert Product Name" />
        </b-form-group>
      <b-form-group id="productPrice" label="Product Price:" label-for="productPrice">
        <b-form-input
          id="productPrice"
          type="text"
          v-model="form.price"
          required
          placeholder="Insert Product Price" />
       </b-form-group>
      <b-form-group id="productQuantity" label="Product Quantity:" label-for="productQuantity">
        <b-form-input
          id="productQuantity"
          type="text"
          v-model="form.quantity"
          required
          placeholder="Insert Product quantity" />
        </b-form-group>
       <b-form-group id="productCode" label="Product Code:" label-for="productCode">
        <b-form-input
          id="productCode"
          type="text"
          v-model="form.code"
          required
          placeholder="Insert Product code" />
        </b-form-group>
       <b-form-group id="productImage" label="Product Image:" label-for="productImage">
        <b-form-input
          id="productImage"
          type="text"
          v-model="form.img"
          required
          placeholder="Insert Product image" />
        </b-form-group>
        <b-button block type="submit" @click="updateProduct" variant="outline-success">Submit</b-button>
        <b-button block type="reset" variant="outline-danger">Reset</b-button>
      </b-form>
    </insert-card>
  </div>
</template>

<script>
import InsertCard from '../components/InsertCard'
import {api} from '../Api'
export default {
  name: 'ProductUpdatePage',
  components: {
    InsertCard
    },

  data() {
    return {
      form: {
        name: '',
        price: '',
        img: '',
        quantity: '',
        code: '',  
      },
      show: true,
    }
  },

  computed: {
    updateTitle: {
      get () { return this.pageTitle },
      set (val) { this.$emit('update:pageTitle', val) }
    }
  },
  methods: {
    updateProduct(){
      api().patch(`updateProduct/${this.$route.params.productId}`, this.form).then((response) => {
         this.$router.push('/product-list')
            console.log(response.data)
               })
           console.log(this.$route.params.productId)
                      },
    product(){
      api().get(`ShowProduct/${this.$route.params.productId}`).then((response) => {
        console.log(response.data)
        this.form.name = response.data.name
        this.form.price = response.data.price
        this.form.quantity = response.data.quantity
        this.form.img= response.data.img
        this.form.code = response.data.code
      })
    },

    onReset(evt) {
      evt.preventDefault()
      /* Reset our form values */ 
      this.form.name = ''
      /* Trick to reset/clear native browser form validation state */
      this.show = false
      this.$nextTick(() => {
      this.show = true
      })
    }
  },
  
  created () {
      this.updateTitle = 'Update porduct'
      // console.log(this.$route.params.productId)
      this.product()
  }
}
</script>

